export function createAction(type) {
    const actionCreator = function (payload) {
        return {
            type,
            payload,
        };
    };
    actionCreator.type = type;
    return actionCreator;
}
export function onAction(action, reduce) {
    return {
        type: action.type,
        reduce,
    };
}
export function createReducer(initialState, ...reducers) {
    return (state = initialState, action) => {
        const reducer = reducers.find((reducer) => reducer.type === action.type);
        if (reducer) {
            return reducer.reduce(state, action.payload);
        }
        return state;
    };
}
